export const routers = [
  {
    path: '/login',
    name: 'Login',
    component:()=> import('@/pages/Login')
  },
  {
    path: '/',
    name: 'layout',
    component:()=> import('@/pages/Layout'),
    children:[
      {
        path: '/home',
        name: 'Home',
        component:()=> import('@/pages/Home')
      },
    ]
    
  },
]



