import axios from 'axios'
import qs from 'qs'
import iview from 'view-design'
import {
  router
} from './router'
window.$router = router;
window.isRefreshing = false
let requests = []

axios.defaults.timeout = 30 * 1000;
axios.interceptors.request.use(config => {
  // console.log(globalconf.axios)
  config.baseURL = globalconf.axios.baseURL;

  const user = sessionStorage.opername
  if (user) {
    config.headers.token = sessionStorage.token
    // config.headers.password = sessionStorage.password?'admin123.':sessionStorage.password
    // config.params = {
    //   opername: sessionStorage.opername,
    //   password:sessionStorage.password?'admin123.':sessionStorage.password,
    //   token:sessionStorage.token,
    //   ...config.params
    // }

  }
  // let params = config.params;
  // let newparam = {};
  // for (let i in params) {
  //   if (params[i] == undefined || params[i] == null || (typeof params[i] == "string" && params[i].trim() == "")) {
  //     continue;
  //   }
  //   newparam[i] = params[i]
  // }
  // config.params = newparam;
  // iview.LoadingBar.start();
  // /* 5.15新增*/
  // let token = sessionStorage.getItem("token");
  // if (token) {
  //   config.headers.token = token;
  // }
  // /* 5.15新增*/
  return config
}, function(error) {
  return Promise.reject(error)
});

axios.interceptors.response.use(async response => {
    iview.LoadingBar.finish()

    let str = response.config.url;
    str = str.replace(response.config.baseURL, "")
    if (response.data.code === -2 && str != '/login') {
      console.log("需要刷新token", window.isRefreshing)
      if (window.isRefreshing) { //已经在刷新token了
        return new Promise(resolve => {
          requests.push((token) => {
            response.config.headers.token = token;
            resolve(axios(response.config))
          })
        })
      } else {
        window.isRefreshing = true
        const form = {
          opername: sessionStorage.opername,
          password: sessionStorage.password
        }
        await axios.post('/login', form).then(async (res) => {
          if (res.code == 0) {
            sessionStorage.token = res.message;
            response.config.headers.token = sessionStorage.token;
            await axios.request(response.config).then(res => {
              response.data = res
            });
            window.isRefreshing = false;
            requests.forEach(cb => cb(sessionStorage.token))
            requests = []
          } else {
            console.log(res)
            console.log("登录信息：", form)
            router.push("/login")
          }

        })
      }
    }
    return response.data
  },

  function(error) {
    iview.LoadingBar.error()
    return Promise.reject(error)
  })
  
export default axios



